.template {
    display: flex;
    flex-direction: column;
    padding: 10px;
    justify-content: center;
    margin-top: 55px;
    width: 100%;
    &__title {
        font-size: 34px;
        margin-bottom: 10px;
        text-align: center;
    }
    &__container {
        display: flex;
        justify-content: center;
    }
    &__search {
        justify-content: flex-start;
    }
}